.moviesMainItem {
  max-height: 381px;
  height: 381px;
  max-width: 180px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  margin-bottom: 20px;
  margin-right: 110px;
  color: #333333;
  cursor: pointer;
  transition: all 0.2ms ease-in-out;
  text-decoration: none;
}
.moviesMainItem:hover {
  background-color: #f6f7f7;
}
.moviesMainItem:hover .name {
  background-color: #004fec;
  color: #fff;
}
.moviesMainItem .image {
  margin-bottom: 20px;
}
.moviesMainItem .image img {
  height: 260px;
  width: 180px;
  border-radius: 20px;
}
.moviesMainItem .name {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 25px;
  transition: all 0.2ms ease-in-out;
  text-transform: uppercase;
}
.moviesMainItem .country {
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 10px;
}
.moviesMainItem .category {
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 18px;
}/*# sourceMappingURL=styles.css.map */