.moviesDetail {
  background-color: #FFFFFF;
  color: #333333;
  border-radius: 20px;
  margin-bottom: 254px;
}
.moviesDetail .detailWrapper {
  display: flex;
  justify-content: left;
  position: relative;
  padding: 0 90px;
  padding-top: 40px;
  padding-bottom: 177px;
}
.moviesDetail .detailWrapper .image {
  margin-right: 23px;
}
.moviesDetail .detailWrapper .image img {
  height: 339px;
  width: 229px;
  border-radius: 20px;
}
.moviesDetail .detailWrapper .data {
  text-transform: uppercase;
}
.moviesDetail .detailWrapper .data .name {
  font-size: 28px;
  font-weight: 700;
  color: #E59C9C;
  margin-bottom: 64px;
}
.moviesDetail .detailWrapper .data .dataWrapper {
  display: flex;
}
.moviesDetail .detailWrapper .data .dataWrapper .leftPart {
  width: 116px;
  font-size: 16px;
  font-weight: 700;
  margin-right: 50px;
}
.moviesDetail .detailWrapper .data .dataWrapper .leftPart p {
  margin-bottom: 22px;
}
.moviesDetail .detailWrapper .data .dataWrapper .rightPart {
  font-size: 14px;
  font-weight: 600;
}
.moviesDetail .detailWrapper .data .dataWrapper .rightPart p {
  margin-bottom: 25px;
}
.moviesDetail .detailWrapper .data .dataWrapper .rightPart .summary {
  text-transform: none;
  font-size: 16px;
  font-weight: 400;
}
.moviesDetail .detailWrapper .rating {
  position: absolute;
  right: 0;
  margin-right: 90px;
  font-size: 27px;
  font-weight: 800;
  display: flex;
}
.moviesDetail .detailWrapper .rating img {
  height: 43px;
  width: 43px;
  margin-right: 11px;
}/*# sourceMappingURL=styles.css.map */