.mainFooter {
  background-color: #3a3a3a;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}
.mainFooter .contentFooter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1230px;
  padding: 57px 0;
}
.mainFooter .contentFooter div {
  font-size: 20px;
  font-weight: 400;
}
.mainFooter .contentFooter div .small {
  font-size: 18px;
  font-weight: 700;
  text-align: right;
}
.mainFooter .contentFooter .author {
  font-weight: 700;
  text-align: right;
