.moviesItem {
  max-height: 229px;
  max-width: 509px;
  width: 509px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  margin-right: 32px;
  color: #333333;
  cursor: pointer;
  transition: all 0.2ms ease-in-out;
  text-decoration: none;
}
.moviesItem:hover {
  background-color: #f6f7f7;
}
.moviesItem:hover .name {
  background-color: #004fec;
  color: #fff;
}
.moviesItem .itemWrapper {
  display: flex;
  justify-content: left;
}
.moviesItem .itemWrapper .image {
  margin-right: 43px;
}
.moviesItem .itemWrapper .image img {
  height: 229px;
  width: 185px;
  border-radius: 20px;
}
.moviesItem .itemWrapper .text .name {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 57px;
  transition: all 0.2ms ease-in-out;
  text-transform: uppercase;
}
.moviesItem .itemWrapper .text .category {
  font-size: 16px;
  font-weight: 400;
}/*# sourceMappingURL=styles.css.map */